.form {
    background: #fff;
    border-radius: 10px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    z-index: 11;
    overflow-y: auto;
    width: 400px;
}

.heading {
    width: 100%;
    text-align: center;
    margin-top: 0;
    margin-bottom: 20px;
}

.inputBlock {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.input {
    font-size: 16px;
    outline: none;
    -webkit-appearance: none;
    -webkit-border: none;
    border: none;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    border-bottom: 2px solid #00BFFF;
    box-sizing: border-box;
    width: 100%;
    padding: 20px 20px 15px 20px;
    color: #000;
    font-size: 20px;
    z-index: 1;
}

.input:focus::placeholder {
    color: #fff;
}

.label {
    font-size: 14px;
    font-weight: bold;
}

.center {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttons {
    width: 100%;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cancel {
    text-decoration: underline;
    font-weight: bold;
    margin-right: 20px;
}

.save {
    background: #00BFFF;
    border-radius: 5px;
    font-weight: bold;
    padding: 15px 20px;
    text-decoration: none;
    color: #000;
}

@keyframes loading {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading {
    display: block;
    margin: 0 auto;
    border: 10px solid #0C4492;
    border-top: 10px solid #00BFFF;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: loading .5s linear infinite;
    cursor: default;
    margin-top: 20px;
}

.dn {
    display: none;
}