@keyframes loading {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading {
    display: block;
    margin: 0 auto;
    border: 10px solid #0C4492;
    border-top: 10px solid #00BFFF;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: loading .5s linear infinite;
    cursor: default;
    margin-top: 20px;
}

.profile {
    margin-bottom: 10px;
    padding: 30px;
    border-radius: 5px;
    background: #00BFFF;
}

.profile:last-child {
    margin-bottom: 0;
}

.image {
    font-size: 30px;
    margin-right: 10px;
}

.link {
    display: inline-flex;
    align-items: center;
    font-weight: bold;
    font-size: 18px;
}

.search {
    margin-bottom: 10px;
}

.input {
    font-size: 16px;
    outline: none;
    -webkit-appearance: none;
    -webkit-border: none;
    border: none;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    border-bottom: 2px solid #00BFFF;
    box-sizing: border-box;
    width: 100%;
    padding: 15px 15px 10px 15px;
    color: #000;
    font-size: 20px;
    z-index: 1;
    border-radius: 5px;
}

.input::placeholder {
    color: #757575;
}

.input:focus::placeholder {
    color: #fff;
}