.btns {
    margin: 20px 0 30px 0;
    display: flex;
}

.link {
    padding: 15px 20px;
    background: #00BFFF;
    border-radius: 5px;
    font-weight: bold;
}

.delete {
    padding: 15px;
    background: red;
    border-radius: 5px;
    margin-left: 10px;
    display: inline-grid;
    place-items: center;
}

.message {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #000080;
    padding: 20px;
    text-align: center;
    border-radius: 10px;
    width: 400px;
    z-index: 11;
}

.active {
    display: block;
}

.text {
    font-size: 20px;
    color: #fff;
}

.submit {
    font-weight: bold;
    display: inline-block;
    background: #00BFFF;
    border-radius: 5px;
    padding: 15px 20px;
    text-decoration: none;
    color: #000;
    transition: .3s;
    margin-right: 10px;
}

.submit:nth-child(3) {
    margin-right: 0;
}

.submit:hover {
    background: #00BFFF;
}